* {
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 70px;
  text-align: center;
  background-color: rgb(174, 174, 245);
  color: rgb(241, 240, 160);
}

.buttonWH {
  color: rgb(128, 120, 230);
  background-color: white;
  width: 500px;
  height: 200px;
  border-radius: 10px;
